@import url(https://fonts.googleapis.com/css?family=Merriweather:100,200,300,300i,400,400i,700);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:400,700);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400);
body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", serif;
  color: #fff; }

.wrapper {
  background: #000;
  background: url(/static/media/meteorits.a0f5f958.jpeg) center center repeat;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center; }

.main {
  height: 90vh;
  box-shadow: 0px 13px 40px -13px rgba(255, 255, 255, 0.75);
  margin: 0 auto; }

.title-container {
  height: 90vh;
  background: url(/static/media/lightning_2.4a38b6bb.jpeg) center center no-repeat;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #000; }
  .title-container__title {
    color: #ec9f0e;
    font-size: 60px;
    letter-spacing: 2px;
    line-height: 1.3;
    font-family: 'monospace', serif; }
  .title-container__subtitle {
    color: #fff;
    font-style: italic;
    font-weight: 100;
    letter-spacing: 1px;
    line-height: 1.5;
    font-family: "Merriweather", serif; }

.form-container {
  background-color: rgba(0, 0, 0, 0.75);
  height: 90vh;
  padding-top: 100px;
  padding-left: 50px; }

input[type="text"] {
  background-color: transparent;
  border: 0;
  border-bottom: solid 1px #ec9f0e;
  width: 30%;
  padding-bottom: 4px;
  color: #fff !important;
  font-weight: lighter;
  letter-spacing: 2px;
  margin-bottom: 30px;
  margin-right: 20px;
  font-size: 20px; }

input[type="text"] {
  outline: none; }

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 30px #2c3e50 inset;
  -webkit-text-fill-color: #fff !important; }

button {
  border: 0;
  padding: 8px 20px;
  margin: 0 2px;
  border-radius: 2px;
  font-weight: lighter;
  letter-spacing: 1px;
  font-size: 15px;
  cursor: pointer;
  background-color: #ec9f0e;
  color: #fff;
  font-weight: 100; }

button:active {
  outline: none; }

.weather__info {
  color: #fff;
  width: 100%;
  font-size: 20px;
  font-weight: 200;
  letter-spacing: 2px; }

.weather__key {
  color: #ec9f0e;
  border-bottom: solid 2px rgba(255, 255, 255, 0.06);
  padding: 20px 0 20px 0;
  font-weight: 400; }
  .weather__key:last-child {
    border: 0; }

.weather__value {
  color: #fff;
  font-weight: 200; }

.weather__error {
  background-color: red;
  color: #fff;
  padding: 10px 5px;
  font-size: 18px;
  letter-spacing: 1px;
  font-weight: 200; }

